<template>
  <v-app>
    <v-app-bar
    app
    id="home-app-bar"
    elevation="1"
    elevate-on-scroll
    height="80"
  >
    

    <v-container class="mx-auto py-0">
      <v-row align="center">
        <v-img
          :src="require('@/assets/nd.svg')"
          class="mr-5"
          contain
          height="48"
          width="48"
          max-width="48"
          @click="$vuetify.goTo(0)"
        />
        
        <v-spacer />

        <div>
          <v-tabs
            class="hidden-sm-and-down"
            optional
            background-color="transparent"
          >
            <v-tab
              v-for="(name, i) in items"
              :key="i"
              :to="{ name }"
              :exact="name === 'Home'"
              :ripple="false"
              class="font-weight-bold"
              min-width="96"
              text
            >
              {{ name }}
            </v-tab>
          </v-tabs>
        </div>
      
      <v-app-bar-nav-icon
      class="hidden-md-and-up"
      @click="toggleDrawer"
    />

      </v-row>
    </v-container>
  </v-app-bar>


  <v-navigation-drawer
    v-model="drawer"
    app
    dark
    temporary
    right
  >
    <v-list>
      <v-list-item
        v-for="(name, i) in items"
        :key="i"
        :to="{ name }"
        @click="onClick($event, link)"
      >
        <v-list-item-title v-text="name" />
      </v-list-item>
    </v-list>
  </v-navigation-drawer>

    <v-main>
      <v-fade-transition mode="out-in">
        <router-view/>
      </v-fade-transition>
    </v-main>



    <v-footer
    color="black"
    dark
    >
      <v-container>
        <v-row>
          <v-col
            cols="12"
            md="6"
          >
          <base-btn
              v-for="(item, i) in social"
              :key="i"
              :href="item.href"
              class="ml-0 mr-3 chevron"
              color="primary"
              square
              target="_blank"
            >
              <v-icon v-text="item.icon" />
          </base-btn>
          </v-col>

          <v-col
            class="text-center text-md-right"
            cols="12"
            md="6"
          >
            &copy; Nate Denys - {{ new Date().getFullYear() }}
            
          </v-col>
        </v-row>
      </v-container>
    </v-footer>
  </v-app>
</template>

<script>
import {
    mapGetters,
    mapMutations,
  } from 'vuex';

export default {
    name: 'App',
    data: () => ({
      items: [
        'Home',
        'Projects',
      ],
      social: [
        {
          name: 'Github',
          href: 'https://github.com/ndenys650',
          icon: 'mdi-github'
        },
        {
          name: 'Instagram',
          href: 'https://www.instagram.com/natedenys/',
          icon: 'mdi-instagram'
        },
        {
          name: 'Linkedin',
          href: 'https://www.linkedin.com/in/nathan-denys',
          icon: 'mdi-linkedin'
        }
      ]
    }),
    computed: {
      ...mapGetters(['links']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          this.setDrawer(val)
        },
      },
    },
    methods: {
      ...mapMutations(['toggleDrawer']),
      ...mapMutations(['setDrawer']),
      onClick (e, item) {
        e.stopPropagation()

        if (item.to === '/') {
          this.$vuetify.goTo(0)
          this.setDrawer(false)
          return
        }

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href)
        this.setDrawer(false)

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href.endsWith('!') ? 0 : item.href)
      }
    }
  }
</script>

<style lang="sass">
#home-app-bar
    .v-tabs-slider
      max-width: 50px
      margin: 0 auto

    .v-tab
      &::before
        display: none

.chevron.v-btn:focus::before
  opacity: 0 !important
</style>
